html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 1.8rem;
  color: #6b7280;
  line-height: 1.3;
}

.tagline {
  font-weight: 600;
  color: #000;
  font-size: 3rem;
  text-align: center;
  margin: 0 0 4rem 0;
}

.page-description {
  text-align: center;
}

/** Media Queries **/
.form-container {
  background-color: #FFFFFF;
  padding: 4rem;
  border-radius: 5rem;
  max-width: 40%;
  margin: 0 auto;
  text-align: center;
}
.form-container .page-description {
  color: #000;
  font-weight: 500;
  font-size: 3rem;
  margin: 0;
}

.form {
  margin-top: 5rem;
}
.form .camp {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.form .camp input, .form .camp textarea {
  flex: 1;
  padding: 2rem;
  border: none;
  border-radius: 2rem;
  background-color: #d8dadf;
}
.form input[type=submit] {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 2rem;
  margin: 0 auto;
  margin-top: 5rem;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .form input[type=submit] {
    width: 100%;
  }
}
.form input[type=submit]:hover {
  background-color: black;
  cursor: pointer;
}

.actions p {
  margin: 1rem;
}

.actions {
  margin-top: 5rem;
  text-align: center;
  color: #000;
}
.actions p, .actions a {
  font-size: 1.6rem;
}
.actions a {
  color: #F1B416;
  font-weight: 500;
}
.actions .verified-login {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 2rem;
  margin: 0 auto;
  margin-top: 5rem;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .actions .verified-login {
    width: 100%;
  }
}
.actions .verified-login:hover {
  background-color: black;
  cursor: pointer;
}

.add-task, .add-project {
  background-color: #000;
  border-radius: 1.2rem;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 600;
  outline: none;
  padding: 1.5rem 4rem;
  text-decoration: none;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  will-change: transform;
}

.add-task:disabled, .add-project:disabled {
  pointer-events: none;
}

.add-task:hover, .add-project:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.add-task:active, .add-project:active {
  box-shadow: none;
  transform: translateY(0);
}

.tasks .toggle-task, .tasks-dash .toggle-task {
  background-color: #000;
  border-radius: 1.2rem;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 600;
  outline: none;
  padding: 1.5rem 4rem;
  text-decoration: none;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  will-change: transform;
}
.tasks .toggle-task:disabled, .tasks-dash .toggle-task:disabled {
  pointer-events: none;
}
.tasks .toggle-task:hover, .tasks-dash .toggle-task:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}
.tasks .toggle-task:active, .tasks-dash .toggle-task:active {
  box-shadow: none;
  transform: translateY(0);
}

.auth-bg {
  background-color: #F1B416;
  min-height: 100vh;
}

.login {
  padding: 4rem 0;
  background-image: url("/build/img/auth-bg.png");
  background-size: 100%;
  background-position: 0rem -12rem;
  min-height: 100vh;
}
.login h1 {
  margin-bottom: 4rem;
}
.login h1 a {
  color: #000;
}

.alerts {
  margin-top: 3rem;
  padding: 0;
}
.alerts .error {
  margin-top: 0.5rem;
  margin-left: 3rem;
  color: rgb(207, 30, 30);
  text-align: left;
}
.alerts .errordiv {
  background-color: rgb(207, 30, 30);
  color: #FFFFFF;
  font-weight: 500;
  border-radius: 2rem;
  padding: 2rem;
  text-align: center;
  list-style: none;
  margin: 0;
  margin-top: 4rem;
}
.alerts .success {
  margin-top: 0.5rem;
  margin-left: 3rem;
  color: rgb(0, 120, 0);
  text-align: left;
}

.topbar-mobile {
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .topbar-mobile {
    display: none;
  }
}
.topbar-mobile .menu img {
  width: 4rem;
}
.topbar-mobile h1 {
  margin: 0;
}
.topbar-mobile a {
  color: #000;
}

.topbar {
  display: none;
}
@media (min-width: 768px) {
  .topbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4rem;
    padding: 5.5rem 4rem 0 10rem;
  }
}
.topbar p {
  margin: 0;
  color: #C6C6C6;
  font-weight: 500;
  font-size: 2rem;
}

.sidebar-mobile {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100%;
  margin-right: -100%;
  transition: all 0.3s ease-in-out;
  z-index: 100;
  color: #FFFFFF;
  background-color: #000;
}
.sidebar-mobile.show {
  margin-right: 0;
}
@media (min-width: 768px) {
  .sidebar-mobile {
    display: none;
  }
}
.sidebar-mobile h1 {
  margin: 0;
}
.sidebar-mobile a {
  color: #FFFFFF;
}
.sidebar-mobile .sidebar-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 4rem;
}
.sidebar-mobile .sidebar-head img {
  width: 4rem;
}
.sidebar-mobile .sidebar-nav {
  margin-top: 5rem;
}
.sidebar-mobile .sidebar-nav a {
  display: block;
  margin-bottom: 1rem;
  padding: 2.2rem 4.3rem;
  color: #FFFFFF;
  font-weight: 500;
}
.sidebar-mobile .sidebar-nav a:hover, .sidebar-mobile .sidebar-nav a.active {
  background-color: #FFFFFF;
  color: #000;
}

.sidebar {
  display: none;
}
@media (min-width: 768px) {
  .sidebar {
    display: block;
  }
}
.sidebar h2 {
  padding: 4rem;
}
.sidebar a {
  color: #000;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  display: block;
  margin-bottom: 1rem;
  padding: 2.2rem 4.3rem;
  color: #000;
  font-weight: 500;
  border-radius: 0 2rem 2rem 0;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.active {
  background-color: #000;
  color: #FFFFFF;
}

.dashboard {
  display: block;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .dash-main {
  flex: 1;
}

.dash-body {
  padding: 4rem;
}
@media (min-width: 768px) {
  .dash-body {
    padding: 0 6rem 10rem 10rem;
  }
}

@media (min-width: 768px) {
  .dash-index {
    display: flex;
  }
}
.dash-index h4 {
  font-size: 2rem;
  margin: 0;
  margin-bottom: 2rem;
}
.dash-index p {
  font-size: 1.6rem;
  line-height: 2rem;
  margin: 1rem 0;
}

.index-projects {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .index-projects {
    width: 65%;
    padding-right: 6rem;
  }
}
.index-projects .projects-title {
  margin-bottom: 4rem;
}
.index-projects .projects-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2rem;
  padding: 0;
}
@media (min-width: 768px) {
  .index-projects .projects-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
.index-projects a {
  color: #FFFFFF;
  background-color: #000;
  padding: 3.5rem 4rem;
  border-radius: 2rem;
  max-height: 20rem;
  cursor: pointer;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  will-change: transform;
}
.index-projects a:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}
.index-projects a:disabled {
  pointer-events: none;
}
.index-projects a:active {
  box-shadow: none;
  transform: translateY(0);
}
.index-projects .project-description {
  color: #C6C6C6;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
}

.index-tasks {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .index-tasks {
    width: 35%;
  }
}
.index-tasks .tasks-title {
  margin-bottom: 4rem;
}

.tasks-dash {
  list-style: none;
  padding: 0;
  gap: 2rem;
  /* CSS */
}
.tasks-dash .no-tasks {
  margin-top: 5rem;
}
.tasks-dash .task-dash {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding: 3.5rem 3.5rem;
  border-radius: 2rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  height: 27rem;
  margin-bottom: 2rem;
}
.tasks-dash .task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tasks-dash .task-name, .tasks-dash .task-description {
  margin: 0;
  font-size: 1.8rem;
}
.tasks-dash .task-name {
  font-weight: 600;
}
.tasks-dash .task-description {
  line-height: 1.2;
  margin-top: 2rem;
  margin-bottom: 2rem;
  max-height: 8rem;
  overflow-y: scroll;
}
.tasks-dash button {
  background-color: transparent;
  border: none;
  padding: 0;
}
.tasks-dash .task-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}
.tasks-dash .task-footer .task-status {
  color: #F1B416;
  font-weight: 500;
  font-size: 1.6rem;
  margin: 0;
}
.tasks-dash .task-footer .task-status.completed {
  color: rgb(0, 120, 0);
}
.tasks-dash .delete-task:hover {
  text-decoration: underline;
  cursor: pointer;
}

.tasks {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  margin-top: 3rem;
  /* CSS */
}
@media (min-width: 768px) {
  .tasks {
    grid-template-columns: repeat(2, 1fr);
  }
}
.tasks .no-tasks {
  margin-top: 5rem;
}
.tasks .task {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding: 3.5rem 4rem;
  border-radius: 2rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  height: 27rem;
}
.tasks .task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tasks .task-name, .tasks .task-description {
  margin: 0;
  font-size: 1.8rem;
}
.tasks .task-name {
  font-weight: 600;
}
.tasks .task-description {
  line-height: 1.2;
  margin-top: 2rem;
  margin-bottom: 2rem;
  max-height: 8rem;
  overflow-y: scroll;
}
.tasks button {
  background-color: transparent;
  border: none;
  padding: 0;
}
.tasks .task-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}
.tasks .task-footer .task-status {
  color: #F1B416;
  font-weight: 500;
  font-size: 1.6rem;
  margin: 0;
}
.tasks .task-footer .task-status.completed {
  color: rgb(0, 120, 0);
}
.tasks .delete-task:hover {
  text-decoration: underline;
  cursor: pointer;
}

.filters {
  margin-top: 5rem;
}
.filters h2 {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .filters h2 {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .filters .filters-inputs {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.filters .camp {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filters .camp {
    margin-bottom: 0;
  }
}
.filters input[type=radio] {
  accent-color: #000;
}

.projectp-description {
  width: 65%;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .form-container {
  max-width: 50rem;
  transform: translateY(-30rem);
  opacity: 0;
  transition: transform 500ms, opacity 200ms;
}
.modal .form-container.animate {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .form {
  margin: 0 auto;
}
.modal .close-modal {
  background-color: transparent;
  border: none;
  font-size: 1.6rem;
  font-weight: 500;
  margin-top: 2rem;
}
.modal .close-modal:hover {
  text-decoration: underline;
  cursor: pointer;
}