@use 'base/variables' as v;
@use 'base/mixins' as m;

.topbar-mobile {
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include m.tablet {
    display: none;
  }

  .menu {
    img {
      width: 4rem;
    }
  }

  h1 {
    margin: 0;
  }

  a {
    color: v.$black;
  }
}

.topbar {
  display: none;

  @include m.tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4rem;
    padding: 5.5rem 4rem 0 10rem;
  }

  p {
    margin: 0;
    color: v.$lightGray;
    font-weight: 500;
    font-size: 2rem;
  }

}

.sidebar-mobile {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100%;
  margin-right: -100%;
  transition: all 0.3s ease-in-out;
  z-index: 100;
  color: v.$white;
  background-color: v.$black;

  &.show {
    margin-right: 0;

    // top: 0;
    // left: 0;
    // right: 0;
    // bottom: 0;
    // color: v.$white;
    // background-color: v.$black;
  }

  @include m.tablet {
    display: none;
  }

  h1 {
    margin: 0;
  }

  a {
    color: v.$white;
  }

  .sidebar-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 4rem;

      img {
        width: 4rem;
      }

  }

  .sidebar-nav {

    margin-top: 5rem;

    a {
      display: block;
      margin-bottom: 1rem;
      padding: 2.2rem 4.3rem;
      color: v.$white;
      font-weight: 500;


      &:hover,
      &.active {
        background-color: v.$white;
        color: v.$black;
      }
    }

  }

}


.sidebar {
  display: none;

  @include m.tablet {
    display: block;
  }

  h2 {
    padding: 4rem;
  }

  a {
    color: v.$black;
  }

  .sidebar-nav {

    margin-top: 5rem;

    a {
      display: block;
      margin-bottom: 1rem;
      padding: 2.2rem 4.3rem;
      color: v.$black;
      font-weight: 500;
      border-radius: 0 2rem 2rem 0;

      &:hover,
      &.active {
        background-color: v.$black;
        color: v.$white;
      }
    }

  }

}
